<template>
  <div>
    <v-stepper v-model="e1">
      <v-stepper-header>
        <v-stepper-step
          :complete="e1 > 1"
          step="1"
        >
          Clientes
        </v-stepper-step>

        <v-divider />

        <v-stepper-step
          :complete="e1 > 2"
          step="2"
        >
          Empreendimento
        </v-stepper-step>

        <v-divider />

        <v-stepper-step 
          :complete="e1 > 3"
          step="3"
        >
          Pagamento
        </v-stepper-step>

        <v-divider />
        
        <v-stepper-step 
          :complete="e1 > 4"
          step="4"
        >
          Corretores
        </v-stepper-step>

        <v-divider />
        
        <v-stepper-step 
          :complete="e1 > 5"
          step="5"
        >
          Resumo
        </v-stepper-step>
      </v-stepper-header>

      <v-stepper-items>
        <v-stepper-content step="1">
          <clientes v-model="model.clientes" />
          
          <div class="d-flex justify-end">
            <v-btn
              color="primary"
              @click="next"
            >
              {{ nextDesc }}
            </v-btn>
          </div>
        </v-stepper-content>

        <v-stepper-content step="2">
          <empreendimento />

          <div class="d-flex justify-space-between">
            <v-btn
              @click="prev"
            >
              {{ backDesc }}
            </v-btn>
            <v-btn
              color="primary"
              @click="next"
            >
              {{ nextDesc }}
            </v-btn>
          </div>
        </v-stepper-content>

        <v-stepper-content step="3">
          <pagamento />
          <div class="d-flex justify-space-between">
            <v-btn
              @click="prev"
            >
              {{ backDesc }}
            </v-btn>
            <v-btn
              color="primary"
              @click="next"
            >
              {{ nextDesc }}
            </v-btn>
          </div>
        </v-stepper-content>
        <v-stepper-content step="4">
          <corretores />
          <div class="d-flex justify-space-between">
            <v-btn
              @click="prev"
            >
              {{ backDesc }}
            </v-btn>
            <v-btn
              color="primary"
              @click="next"
            >
              {{ nextDesc }}
            </v-btn>
          </div>
        </v-stepper-content>
        <v-stepper-content step="5">
          <resumo :model="model" />
          <div class="d-flex justify-space-between">
            <v-btn
              @click="prev"
            >
              {{ backDesc }}
            </v-btn>
            <v-btn
              color="primary"
              @click="enviar"
            >
              {{ sendDesc }}
            </v-btn>
          </div>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>
  </div>
</template>
<script>
import Clientes from './Clientes.vue';
import Corretores from './Corretores.vue';
import Empreendimento from './Empreendimento.vue';
import Pagamento from './Pagamento.vue';
import Resumo from './Resumo.vue';
export default {
    components: { 
      Clientes,
       Empreendimento,
        Pagamento,
         Corretores,
          Resumo 
          },
    data(){
        return {
            model:{
              clientes:[],
            },
            e1: 1,
            backDesc:'Voltar',
            nextDesc:'Prosseguir',
            sendDesc:'Enviar'
        }
    },
    methods:{
      next(){
        this.e1++;
      },
      prev(){
        this.e1--;
      },
      enviar(){
        alert('Sucesso!!');
      }
    }
}
</script>