<template>
  <div>
    <v-form v-model="isValid">
      <v-container>
        <v-row>
          <v-col
            cols="12"
            sm="4"
            md="3"
          >
            <v-text-field
              v-model="value.id"
              label="Id"
              disabled
            />
          </v-col>

          <v-col
            cols="12"
            md="6"
          >
            <v-text-field
              v-model="value.nome"
              :rules="regrasNome"
              label="Nome"
            />
          </v-col>

          <v-col
            cols="12"
            sm="4"
            md="3"
          >
            <v-text-field
              v-model="value.percentualCompra"
              suffix="%"
              type="number"
              :rules="regrasPercentual"
              label="Percentual"
            />
          </v-col>
        </v-row>
      </v-container>
    </v-form>


    <v-btn
      color="primary"
      @click="complete(true)"
    >
      Concluir
    </v-btn>
    <v-btn
      color="error"
      @click="complete(false)"
    >
      Cancelar
    </v-btn>
  </div>
</template>
<script>
export default {
    props:{
        value: {
            type: Object,
            required: true
        },
    },
    data(){
      return {
        isValid: false,
        regrasNome:[
          v => !!v || 'Nome obrigatório'
        ],
        regrasPercentual:[
          p => (parseFloat(p || 0) > 0 && parseFloat(p || 0) <= 100) || 'Percentual deve ser entre 0% e 100%'
        ]
      }
    },
    updated(){
        this.$emit('input', this.value);
    },
    methods:{
        complete(sucesso){
            if(!this.isValid){
              alert('Ta errado');
              return;
            }

            this.$emit('complete', sucesso);
        }
    }
}
</script>