<template>
  <div>
    <div v-if="!estaEditando">
      <v-data-table
        :headers="headers"
        :items="listaClientes"
        :items-per-page="5"
        class="elevation-1"
      >
        <template v-slot:top>
          <div class="d-flex justify-end">
            <v-btn
              color="primary"
              @click="editarCliente()"
            >
              Adicionar cliente
            </v-btn>
          </div>
        </template>
        <template v-slot:item.acoes="{ item: item }">
          <v-icon
            small
            class="mr-2"
            @click="editarCliente(item.id)"
          >
            mdi-pencil
          </v-icon>
          <v-icon
            small
            @click="deletarCliente(item.id)"
          >
            mdi-delete
          </v-icon>
        </template>
      </v-data-table>
    </div>
    <div v-else>
      <cliente
        v-model="actualClient"
        @complete="edicaoClienteCompleta($event)"
      />
    </div>
  </div>
</template>
<script>
import Cliente from './Cliente.vue'
export default {
    components: { Cliente },
    props:{
        value: {
            type: Array,
            required: true
        },
    },
    data(){
        return {
            headers:[
                {text:'Id', value:'id', sortable: true},
                {text:'Nome', value:'nome', sortable: true},
                {text: 'Perc. Compra', value:'percentualCompra'},
                { text: 'Ações', value: 'acoes', sortable: false },
            ],
            listaClientes: [
              {id:1,nome:'Wilmar', percentualCompra: '50%'},
              {id:2,nome:'Paulo', percentualCompra: '50%'},
            ],
            estaEditando: false,
            actualClient:{},
        }
    },
    updated(){
      this.$emit('input', this.listaClientes);
    },
    methods:{
        edicaoClienteCompleta(sucesso){
            if(sucesso){
                var idCliente = parseInt((this.actualClient && this.actualClient.id) || 0);
                if(idCliente > 0){
                  var indexClienteExistente = this.listaClientes.findIndex((val) => val.id == idCliente);
                  this.listaClientes.splice(indexClienteExistente, 1, this.actualClient);
                }else{
                  let idsOrdenados = this.listaClientes.map(val => parseInt(val.id || 0)).sort((a,b) => b - a);
                  if(idsOrdenados.length){
                    this.actualClient.id = idsOrdenados[0] + 1;
                  }else{
                    this.actualClient.id = 1;
                  }
                  this.listaClientes.push(this.actualClient);
                }
            }

            this.estaEditando = false;
            this.actualClient = null;
        },
        editarCliente(id){
            id = parseInt(id || 0);
            let cliente = this.listaClientes.find((val) => val.id == id);
            this.actualClient = cliente || {};
            this.estaEditando = true;
        },
        deletarCliente(id){
          id = parseInt(id || 0);
          var indexClienteExistente = this.listaClientes.findIndex((val) => val.id == id);
          this.listaClientes.splice(indexClienteExistente, 1);
        }
    }
}
</script>